import { createTheme } from '@mui/material';
import './App.css';
import AppRoutes from './components/appRoutes';
import { ThemeProvider } from '@emotion/react';

const theme = createTheme({
  palette: {
    // primary: {
    //   main: 'rgb(255, 255, 255)', //   
    // },
    // secondary: {
    //   main: 'rgb(255, 255, 255)', //    
    // },
    //       
  },
  typography: {
    //        
  },
});

function App() {
  return (
    < ThemeProvider theme={theme}>
      <AppRoutes></AppRoutes>
    </ThemeProvider>
  );
}

export default App;
